/* Body styling */
body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #e0f2fe, #bae6fd);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/* Form container */
form {
  background: #ffffff;
  padding: 40px 35px;
  border-radius: 14px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
  width: 360px;
  text-align: center;
}

/* Heading */
h1 {
  margin-bottom: 20px;
  color: #0284c7;
  font-size: 28px;
  font-weight: 600;
}

/* Labels */
label {
  display: block;
  font-size: 14px;
  color: #0f172a;
  margin-top: 15px;
  text-align: left;
}

/* Input fields */
input {
  width: 100%;
  padding: 12px;
  margin-top: 5px;
  border: 1px solid #cbd5e1;
  border-radius: 8px;
  outline: none;
  font-size: 14px;
  transition: 0.3s;
}

input:focus {
  border-color: #0ea5e9;
  box-shadow: 0 0 8px rgba(14, 165, 233, 0.3);
}

/* Login button */
button {
  width: 100%;
  background-color: #0ea5e9;
  color: white;
  border: none;
  padding: 12px;
  margin-top: 20px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  transition: background 0.3s ease;
}

button:hover {
  background-color: #0369a1;
}

/* Signup text */
.signup-text {
  font-size: 14px;
  margin-top: 15px;
  color: #334155;
}

.signup-link {
  color: #0ea5e9;
  font-weight: bold;
  cursor: pointer;
  text-decoration: underline;
  transition: color 0.2s ease;
}

.signup-link:hover {
  
  color: #0369a1;
}
/* Add margin-top to push link below button */
.forgot-password {
  margin-top: 15px;   /* âœ… Space between Login button and link */
  text-align: center;
}

.forgot-password a {
  color: #555; /* dark grey */
  text-decoration: none;
  font-size: 14px;
}

.forgot-password a:hover {
  color: #333;
  text-decoration: underline;
  
}


